.container {
  width: 100vw;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.background {
  position: fixed;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  z-index: -1;
  opacity: 0;
}
.background img {
  width: 105%;
  height: 105%;
  filter: blur(12px);
  margin: -20px;
  align-self: center;
}

.videos__container {
  width: 98%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  gap: 3px;
  justify-content: center;
}

.titulo{
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 100vw;
  margin: 15px 0;
}
.titulo a{
  display: flex;
  font-family: "Oswald", sans-serif;
  gap: 5px;
  align-items: center;
  color: #FFFFFF;
  text-decoration: none;
}

h1 {
  font-family: "Oswald", sans-serif;
  color: #f7f6f6;
  font-size: 1.5em;
}

.sendVideo{
  position: fixed;
  bottom: 5px;
  right: 15px;
  display: flex;
  align-items: center;
  color: #f7f6f6;
  font-family: "Oswald", sans-serif;
  z-index: 120;
}
