#video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.video__container{
    position: relative;
    height: 10vh;
    width: 10vw;
}

#video::-moz-media-controls {
  display: none !important;
}

#video::-ms-media-controls {
  display: none !important;
}

#video::media-controls {
  display: none !important;
}

#video::media-controls-overlay {
  display: none !important;
}

.eye{
  width: 18px;
  height: 18px;
  position: absolute;
  z-index: 100;
  bottom: 3px;
  right: 3px;
}
.eye img{
  height: 100%;
  width: 100%;
}

@media all and (min-width: 480px) and (max-width: 780px) { 
  .video__container{
    width: 23vw;
  }
}
@media all and (max-width: 480px) { 
  .video__container{
    width: 23vw;
  }
}